				"tipoProposicao": "Projeto de Lei",
				"tema": "Administração Pública; Direito Civil e Processual Civil; Direito Penal e Processual Penal; Organização Política, Partidária e Eleitoral",
				"ementa": "Estabelece medidas contra a corrupção e demais crimes contra o patrimônio público e combate o enriquecimento ilícito de agentes públicos.\n\nNOVA EMENTA: Estabelece medidas de combate à impunidade, à corrupção; altera os Decretos-Leis nºs 2.848, de 7 de dezembro de 1940 - Código Penal, e 3.689, de 3 de outubro de 1941 - Código de Processo Penal; as Leis nºs 4.717, de 29 de junho de 1965, 4.737, de 15 de julho de 1965, 8.072, de 25 de julho de 1990, 8.112, de 11 de dezembro de 1990, 8.429, de 2 de junho de 1992, 8.906, de 4 de julho de 1994, 9.096, de 19 de setembro de 1995,  9.504, de 30 de setembro de 1997, 9.613, de 3 de março de 1998, e 7.347, de 24 de julho de 1985; revoga dispositivos do Decreto-Lei nº 201, de 27 de fevereiro de 1967, e da Lei nº 8.137, de 27 de dezembro de 1990; e dá outras providências.",
				"explicacaoEmenta": "Altera as Leis nº 8.137, de 1990; 8.702, de 1990; 13.150,  de 2015;  8.429, de 1992; 9.096, de 1995; 9.504, de 1997; 9.613, de 1998 e os Decretos-leis nº 2.848, de 1940; 3.689, de 1941. Revoga dispositivo do Decreto-lei nº 201, de 1967.",
				"dataApresentacao": "29/03/2016",
				"regimeTramitacao": "Urgência (Art. 155, RICD)",
				"dataUltimoDespacho": "17/02/2017",
				"ultimoDespacho": "Encaminhe-se à Secretaria-Geral da Mesa, nos termos do art. 252, V, do Regimento Interno da Câmara dos Deputados, para fins de verificação do atendimento do que dispõe o art. 61, § 2º, da Constituição Federal. Informo que, excepcionalmente, serão levados em conta, para fins de certificação do cumprimento dos requisitos constitucionais referentes à propositura de projeto de lei de iniciativa popular, todos os registros que apresentarem informações completas, em atenção à decisão liminar em epígrafe. Em seguida, submeta-se o processo ao Presidente da Comissão de Constituição e Justiça e de Cidadania para análise e ratificação do que certificado pela Secretaria-Geral da Mesa. Publique-se. Oficie-se.",
				"apreciacao": "Proposição Sujeita à Apreciação do Plenário",
				"indexacao": "Alteração, Código Penal, crime praticado por funcionário público contra a administração pública, tipicidade, crime, enriquecimento ilícito, prisão, confisco de bens, agravação penal, aumento, pena, peculato, concussão, excesso de exação (crime), corrupção passiva, corrupção ativa, estelionato, sistema, prescrição, crime, extinção, prescrição retroativa, unificação, prazo prescricional, pretensão punitiva, pretensão executória, pena cominada  duplicação, prazo, prescrição, corrupção ativa, transação comercial, âmbito internacional. \n_ Alteração, Lei dos Crimes contra a Ordem Tributária, supressão, dispositivo legal, regulação, especificação, crime, corrupção. _Alteração, Lei dos Crimes Hediondos, tipicidade, crime hediondo, peculato, inserção de dados falsos em sistema de informação, concussão, excesso de exação(crime), corrupção passiva, corrupção ativa. _Alteração, Código de Processo Penal,  recusa, tribunal de justiça, recurso judicial, recurso protelatório, prazo determinado, pedido de vista, disciplinamento, embargo de declaração, habeas corpus, recurso especial, recurso extraordinário, agilização,  justiça, conceituação, prova ilícita, critério, inadmissibilidade, exclusão, ilicitude, preclusão, nulidade. _Alteração, Código de Processo Civil, recusa, tribunal de justiça, recurso judicial, recurso protelatório, prazo determinado, pedido de vista. _Alteração, Lei da Improbidade Administrativa, disciplinamento, acordo de leniência, pessoa física, pessoa jurídica, improbidade administrativa, Ministério Público. _Alteração, Lei Orgânica dos Partidos Políticos (1995), Lei das Eleições (1997), partido político, ato ilícito, administração pública, país estrangeiro, utilização indevida, recursos não contabilizados, bens, procedência, infração penal, sanção, multa, ressarcimento, suspensão, cancelamento,  registro, prazo, competência, Justiça Eleitoral. _Alteração, Lei de Lavagem de Dinheiro, disciplinamento, cumprimento, instituição financeira, ordem judicial, quebra, transferência, sigilo bancário, sigilo fiscal, rastreamento, recursos financeiros, descumprimento, multa. _Disciplinamento, ação civil pública, perda civil de bens, atividade ilícita. _Revogação, dispositivo legal, decreto-lei, crime de responsabilidade, prefeito, apropriação indébita, bens. _Critério, percentual, recursos financeiros, publicidade, programa, marketing, combate, corrupção, Administração Pública, Código de conduta.",
